<template>
  <div>
    <v-card>
      <v-card-title primary-title>
        <div>
          <div class="headline">Patient Form: Dr. {{doctor.last}} ({{doctor.id}})</div>
          <span class="grey--text">Please fill out the information below to check-in. Thank you.</span>
        </div>
      </v-card-title>
      <v-layout row class="top generationParams">
        <v-flex xs3 class="space">
          <v-text-field v-model="patient.first" label="First Name:"></v-text-field>
        </v-flex>
        <v-flex xs3 class="space">
          <v-text-field v-model="patient.last" label="Last Name:"></v-text-field>
        </v-flex>
        <v-flex xs3 class="space">
          <v-text-field v-model="patient.ssn" label="SSN:"></v-text-field>
        </v-flex>
      </v-layout>
      <v-layout row class="generationParams" v-if="verified">
        <v-flex xs3 class="space">
          <v-text-field v-model="patient.city" label="City:"></v-text-field>
        </v-flex>
        <v-flex xs3 class="space">
          <v-text-field v-model="patient.state" label="State:"></v-text-field>
        </v-flex>
        <v-flex xs3 class="space">
          <v-text-field v-model="patient.ethnicity" label="Ethnicity:"></v-text-field>
        </v-flex>
      </v-layout>
      <v-layout row class="bottom">
        <v-btn secondary :loading="loading" :disabled="loading" @click="verify()">{{submitText}}</v-btn>
      </v-layout>
    </v-card>
  </div>
</template>

<script>
import chrono from '../api/chrono'
export default {
  name: 'patient',
  props: {
    doctor: Object,
    action: Function
  },
  data() {
    return {
      patient: {
        first: '',
        last: '',
        ssn: '',
        city: '',
        state: '',
        ethnicity: ''
      },
      verified: false,
      loading: false
    }
  },
  computed: {
    submitText: function() {
      return (!this.verifed) ? 'Submit' : 'Finalize / Complete'
    }
  },
  methods: {
    verify() {
      this.loading = true
      chrono.verifyAppointment({
        first: this.patient.first,
        last: this.patient.last,
        ssn: this.patient.ssn,
        doctor_id: this.doctor.id
      }).then((response) => {
        this.verfied = response.status !== 'error'
        if (this.verfied) {
          this.patient = response.patient
        }
        this.action(response.status, response.msg)
        this.loading = false
      })
    }
  }
}
</script>

<style scoped>
.generationParams {
  margin-left: 15px;
}

.top {
  margin-top: 15px;
}

.bottom {
  padding-bottom: 10px;
  margin-left: 5px;
}

.space {
  margin-right: 15px;
}
</style>



